.SuperInputText_group__2FIMM 			  {
    position:relative;
    margin-bottom:40px;
}
input 				{
    font-size:18px;
    padding:10px 0px 10px 0px;
    display:block;
    width: 100%;
    border:none;
    border-bottom:1px solid #21268F;

    background: #F9F9FE;
}
input:focus 		{ outline:none; }

/* LABEL ======================================= */
label {
    color:#999;
    font-size:18px;
    font-weight:normal;
    position:absolute;
    pointer-events:none;
    left:5px;
    top:10px;
    transition:0.2s ease all;
    -moz-transition:0.2s ease all;
    -webkit-transition:0.2s ease all;
}

/* active state */
input:focus ~ label, input:valid ~ label 		{
    top:-20px;
    font-size:14px;
    color:#21268F;
}

/* BOTTOM BARS ================================= */
.SuperInputText_bar__1qdVa 	{ position:relative; display:block; width:100%; }
.SuperInputText_bar__1qdVa:before, .SuperInputText_bar__1qdVa:after 	{
    content:'';
    height:2px;
    width:0;
    bottom:1px;
    position:absolute;
    background:#5264AE;
    transition:0.2s ease all;
    -moz-transition:0.2s ease all;
    -webkit-transition:0.2s ease all;
}
.SuperInputText_bar__1qdVa:before {
    left:50%;
}
.SuperInputText_bar__1qdVa:after {
    right:50%;
}

/* active state */
input:focus ~ .SuperInputText_bar__1qdVa:before, input:focus ~ .SuperInputText_bar__1qdVa:after {
    width:50%;
}

/* HIGHLIGHTER ================================== */
.SuperInputText_highlight__3tJ8R {
    position:absolute;
    height:60%;
    width:100px;
    top:25%;
    left:0;
    pointer-events:none;
    opacity:0.5;
}

/* active state */
input:focus ~ .SuperInputText_highlight__3tJ8R {
    animation:SuperInputText_inputHighlighter__3HjP1 0.3s ease;
}

/* ANIMATIONS ================ */
@keyframes SuperInputText_inputHighlighter__3HjP1 {
    from { background:#5264AE; }
    to 	{ width:0; background:transparent; }
}







